
/*Flex mixin*/
.inline-flex,
.flex-inline,
.mixin-inline-flex {

    display: inline-flex;
}
.flex,
.flex-row,
.mixin-flex-row {

    display: flex;
    flex-wrap: wrap;
}
.flex-row-nowrap,
.mixin-flex-row-nowrap {

    display: flex;

    flex-wrap: nowrap;
}
.row-reverse,
.flex-row-reverse,
.mixin-flex-row-reverse {
    -ms-flex-direction: row-reverse;
    flex-direction: row-reverse;
}
.flex-column,
.mixin-flex-column {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    -ms-flex-direction: column;
    flex-wrap: wrap;
    flex-direction: column;
}
.flex-column-reverse,
.mixin-flex-column-reverse {
    -ms-flex-direction: column-reverse;
    flex-direction: column-reverse;
}
.flex-center,
.mixin-flex-center {
    align-items: center;
    justify-content: center;
}

