/* Page - Blog Inner */

.blog-inner-page .padding {
  max-width: 780px;
}

.blog-inner-page ul.socials li a {
  color: $textDark;
  background: transparent;
}

.blog-inner-page ul.socials li a:hover {
  color: $textDark;
  background: $default;
}

.blog-inner-page .heading h1 {
  font-size: $title-lg;
  color: $textDark;
  font-weight: 600;

  @media screen and (max-width: 1199px) {
    font-size: $title-md;
  }

  @media screen and (max-width: 767px) {
    font-size: $title-sm;
  }
}

.blog-inner-page .heading-image {
  width: 100%;
  //height: 70vh;
  //-webkit-background-size: cover;
  //background-size: cover;
  //background-position: center center;
  //background-repeat: no-repeat;
  //background-attachment: fixed;
}

.blog-inner-page .description img {
  max-width: 100%;
}

.blog-inner-page .share-label {
  font-size: 18px;
  color: $textDark;
  font-weight: bold;
}

.blog-inner-page .comments .comment-holder {
  text-align: center;
}

.blog-inner-page .comments .comment-holder .author,
.blog-inner-page .comments .comment-holder .author a,
.blog-inner-page .comments .comment-holder .author span {
  font-size: 20px;
  color: $default;
  font-weight: bold;
  text-decoration: none;
}

.blog-inner-page .comments .comment-holder .date {
  margin-bottom: 20px;
}

.blog-inner-page .comments .comment-holder .date,
.blog-inner-page .comments .comment-holder .date span {
  font-size: 12px;
  color: #9f9f9f;
}

.blog-inner-page .comments .comment-holder .description {
  width: 80%;
  margin: 0 auto;
}

.blog-inner-page .comments .comment-holder .description,
.blog-inner-page .comments .comment-holder .description p {
  font-size: 16px;
  color: $textDark;
  text-align: center;
}

@media screen and (max-width: 991px) {

  .blog-inner-page .heading h1 {
    font-size: 30px;
  }
}

@media screen and (max-width: 767px) {
  .blog-inner-page .heading-image {
    width: 100%;
    //height: 30vh;
    -webkit-background-size: contain;
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: inherit;
  }

  .blog-inner-page .heading h1 {
    font-size: 28px;
  }
}

.blog-inner-page .reviews {
  margin-top: 20px;
}

.blog-inner-page .reviews .heading h3 {
  font-size: $paragraph-lg;
  color: $textDark;
  font-weight: bold;
  padding: 8px 0 25px 0px;
}

.blog-inner-page .reviews .heading i {
  float: left;
  font-size: 50px;
  color: $default
}

.blog-inner-page .reviews .review {
  background: $silver;
  padding: 15px 0;
  color: $textOnWhite;
  margin-bottom: 20px;
}

.blog-inner-page .reviews .review:after {
  clear: both;
  content: '';
  display: block;
}

.blog-inner-page .reviews .review .avatar-holder {
  width: 70px;
  float: left;
}

.blog-inner-page .reviews .review .avatar-holder .avatar {
  background: $silver;
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  background-size: cover;
}

.blog-inner-page .reviews .review .info {
  width: calc(100% - 70px);
  float: left;
}

.blog-inner-page .reviews .review .info h4 {
  color: $textOnWhite;
  font-size: 14px;
  font-weight: 700;
  margin: 10px 0 20px 0;
}

.blog-inner-page .reviews .review .info p {
  color: $textOnWhite;
  font-size: 14px;
}

.blog-inner-page .leave-review .left-side {
  float: left;
  width: 70px;
}

.blog-inner-page .leave-review .right-side {
  float: left;
  width: calc(100% - 70px);
}

.blog-inner-page .leave-review .left-side i {
  font-size: 50px;
  color: $textOnWhite
}

.blog-inner-page .leave-review .right-side h3 {
  font-size: 24px;
  color: $textOnWhite;
  font-weight: bold;
  margin: 12px 0 25px 0;
}

.blog-inner-page .leave-review .right-side p {
  font-size: 16px;
  color: $textOnWhite;
}

.blog-inner-page .leave-review .form textarea {
  min-height: 100px;
  max-width: 100%;
}

.blog-inner-page .leave-review .form .form-group {
  margin-bottom: 25px;
}

.blog-inner-page .leave-review .form .form-group label {
  font-size: 14px;
  color: $textOnWhite;
  line-height: 1.4;
  margin-bottom: 10px;
}

.blog-inner-page .leave-review .form .form-group .form-control {
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  border: 2px solid #ddd;
  color: $textOnWhite;
  font-size: 0.857rem;
  padding: 12px 20px;
  margin-bottom: 10px;
  margin-right: 10px;
  font-weight: 600;
  height: auto;
}

.blog-inner-page .leave-review .form .form-group .form-control::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: fade-in($grey, .8);
  opacity: 1; /* Firefox */
}

.blog-inner-page .leave-review .form .form-group .form-control:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: fade-in($grey, .8);
}

.blog-inner-page .leave-review .form .form-group .form-control::-ms-input-placeholder { /* Microsoft Edge */
  color: fade-in($grey, .8);
}

.blog-inner-page .reviews {
  h1, h2, h3, h4, h5, h6, p, span {
    color: $textOnWhite !important;
  }
}

