.zoomContainer {
  z-index: 3;
}

.shop-inner-page {
  .elevatezoom {
    position: relative;
    margin-bottom: 40px;

    .main-image {
      max-width: 100%;
    }

    .square {
      border: 1px solid $silver;

      .content {
        padding: 20px;
      }
    }

    .elevatezoom-holder {

      height: 100%;
      white-space: nowrap;
      max-height: 100%;
      text-align: center;
      max-width: 100%;
      margin: 0 auto;
      align-items: center;
      justify-content: center;
      display: flex;

      position: relative;

      img {
        max-height: 100%;
        /*position: relative !important;*/
        left: 0;
        right: 0;
        margin: 0 auto;
      }

      .helper {
        display: inline-block;
        height: 100%;
        vertical-align: middle;
      }

      .zoomWrapper {
        max-height: 100%;
        max-width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        //display: flex;
        padding: 10px;
      }

    }

    #elevatezoom-gallery {
      margin-top: 20px;
      position: relative;
      padding: 0 25px;
      width: 100%;
      float: left;

      .slick-list {
        margin: 0 -10px;
      }

      a {
        display: block;
        height: 100px;
        width: 100%;
        background-size: contain;
        background-repeat: no-repeat;
        background-origin: content-box;
        padding: 5px 10px;
        margin: 0px 0 14px 0;
        background-color: #fff;
        background-position: center center;
        border: 1px solid #ebebeb;
        margin: 10px;
      }

      a.active {
        border-color: $primary;
        -webkit-box-shadow: 0 0 17px rgba(200, 200, 200, 0.5);
        -moz-box-shadow: 0 0 17px rgba(200, 200, 200, 0.5);
        box-shadow: 0 0 17px rgba(200, 200, 200, 0.5);
      }

      .slick-arrow {
        //position: absolute;
        top: 60px;
        z-index: 4;
        font-size: 0;
        border: 0;
        background: none;
        //display: none !important;
      }

      &:hover .slick-arrow {
        display: block !important;
      }

      .slick-arrow:before {
        font-family: FontAwesome;
        color: $grey;
        display: block;
        font-size: 12px;
        text-shadow: 0px 2px 2px rgba(255, 255, 255, 0.8);
      }

      .slick-next {
        right: 0;
      }

      .slick-next:before {
        content: '\f054';
      }

      .slick-prev {
        left: 0;
      }

      .slick-prev:before {
        content: '\f053';
      }

      .slick-track {
        float: left;
      }

    }
  }
}

/* Slick options */
.product-gallery .slick-arrow {
  position: absolute;
  top: 45%;
  z-index: 4;
  font-size: 0;
  border: 0;
  background: none;
  display: none !important;
}

.shop-inner-page .product-gallery:hover .slick-arrow {
