    line-height: normal;
    letter-spacing: normal;

    @media screen and (max-width: 1199px) {
      font-size: $title-xs;
    }
  }

}

/* Masonry Gallery */
.masonry-gallery .image-holder {
  position: relative;
  overflow: hidden;
  z-index: 60;
}

.masonry-gallery .image-holder:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  border: 10px solid $light;
  z-index: 50;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  -webkit-transform: scale(1.06);
  -ms-transform: scale(1.06);
  transform: scale(1.06);
}

.masonry-gallery .image-holder:hover:after {
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
}

.masonry-gallery .image-holder:hover .image-holder__descr {
  opacity: 1;
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateY(0);
}

.masonry-gallery .image-holder:hover .image-holder__icon {
  opacity: 1;
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  transform: translateY(0);
}

.masonry-gallery .image-holder_dark:after {
  border-color: #2b2b2b;
}

.masonry-gallery .image-holder__img {
  width: 100%;
  height: auto;
}

.masonry-gallery .image-holder__descr {
  position: absolute;
  left: 0;
  bottom: 10px;
  width: 100%;
  background-color: $primary;
  text-align: center;
  padding: 17px 0px;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  opacity: 0;
  -webkit-transform: translateY(10px);
  -ms-transform: translateY(10px);
  transform: translateY(10px);
}

.projects .masonry-gallery .image-holder__descr {
  background-color: fade($primary, 80%);
  text-align: left;
  padding: 15px 20px;
}

.projects .masonry-gallery .image-holder__descr .image-holder__heading {
  color: $default;
  font-size: 16px;
  text-transform: none;
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.projects .masonry-gallery .image-holder__descr .image-holder__description {
  color: $light;
  font-size: 16px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.masonry-gallery .image-holder__heading {
  font-size: 1.07692rem;
  font-weight: 400;
  color: $light;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 0;
}

.masonry-gallery .image-holder__icon {
  display: inline-block;
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  background-color: $default;
  text-align: center;
  padding: 3px;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  opacity: 0;
  -webkit-transform: translate(10px, -10px);
  -ms-transform: translate(10px, -10px);
  transform: translate(10px, -10px);
}

.masonry-gallery .image-holder__icon:before {
  content: '\f105';
  display: inline-block;
  font-family: 'FontAwesome';
  font-size: 1.23077rem;
  color: $dark;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.masonry-gallery .masonry-grid-works {
  margin: 0 -10px;
}

.masonry-gallery .masonry-grid-works_fullwidth {
  margin: 0;
}

.masonry-gallery .masonry-grid-works__sizer_w-20 {
